import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Item } from './item';
import { Observable } from 'rxjs';

@Injectable
({
  providedIn: 'root'
})

export class ItemService
{
  constructor(private httpClient: HttpClient) {}


  public getAllItems(): Observable<Item[]>
  {
    return this.httpClient.get<Item[]>("/items/get-all-items/");
  }
  
  public getItem(no: number): Observable<Item>
  {
    return this.httpClient.get<Item>("/items/get-item/"+no);
  }

  public withdrawalQty(no: number, withdrawal: number)
  {
    return this.httpClient.put("/items/withdrawal-qty/"+no+"/"+withdrawal, {responseType: "number"});
  }

  public depositQty(no: number, deposit: number)
  {
    return this.httpClient.put<number>("/items/deposit-qty/"+no+"/"+deposit, {responseType: "number"});
  }

  public addItem(item: Item)
  {
    return this.httpClient.post<Item>("/items/add-item", item);
  }

  public deleteItem(no: number)
  {
    return this.httpClient.delete("/items/delete-item/"+no, {responseType: "text"});
  }
}