import { Component, OnInit } from '@angular/core';
import { Item } from 'src/app/item';
import { ItemService } from 'src/app/item.service';

@Component
({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.css']
})

export class AddItemComponent implements OnInit
{
  items: Item[];
  name: string;
  qty: number;
  code: string;
  item: Item;
  check: boolean=true;
  added: boolean;

  constructor(private itemService: ItemService) {}

  ngOnInit()
  {
    this.itemService.getAllItems().subscribe(result=>
    {
      this.items=result;
    });
  }

  public addItem()
  {
    for(let i=0; i<this.items.length; i++)
    {
      if(this.items[i].name==this.name)
      {
        alert("The name you've entered already exists.");
        this.check=false;
        break;
      }
      else if(this.items[i].code==this.code)
      {
        alert("The inventory code you've entered already exists.");
        this.check=false;
        break;
      }
      else
      this.check=true;
    }

    if(this.check==true)
    {
      if(this.qty<0)
        alert("Minimum quantity is 0.");
      else
      {
        this.item=new Item(0, this.name, this.qty, this.code);

        this.itemService.addItem(this.item).subscribe(result=>
        {
          this.added=true;
        });
      }
    }
  }
}