import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './components/home/home.component';
import { GetItemComponent } from './components/get-item/get-item.component';
import { GetAllItemsComponent } from './components/get-all-items/get-all-items.component';
import { AddItemComponent } from './components/add-item/add-item.component';
import { DeleteItemComponent } from './components/delete-item/delete-item.component';
import { ErrorComponent } from './components/error/error.component';

const routes: Routes=
[
  {path: "home", component: HomeComponent},
  {path: "", redirectTo: "home", pathMatch: "full"},
  {path: "get-item", component: GetItemComponent},
  {path: "get-all-items", component: GetAllItemsComponent},
  {path: "add-item", component: AddItemComponent},
  {path: "delete-item", component: DeleteItemComponent},
  {path: "**", component: ErrorComponent}
];

@NgModule
({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {}